import axios from "axios";
import { profileNameText } from "../modules/dashboard";
export const handleDashboard = () => {
  const urlParams = new URLSearchParams(window.location.search);
  const emailValue = urlParams.get('email');

  axios.post('https://jr.paswad.com/user/', {
      email: emailValue,
    })
    .then(response => {
      const data = response.data;
      if (data.response.code === 1) {
        const userDetails = data.response.payload.userDetails;
        profileNameText.innerText = data.response.payload.userDetails.email;
        console.log(userDetails)
        // document.getElementById('usernameDisplay').innerText = userDetails.username || 'N/A';
        // document.getElementById('emailDisplay').innerText = userDetails.email || 'N/A';
        // document.getElementById('dobDisplay').innerText = userDetails.dob || 'N/A';
        // document.getElementById('addressDisplay').innerText = userDetails.address || 'N/A';
      } else {
        const userDetails = data.response.payload.userDetails; 

      }
    })
    .catch(error => {
      console.error('There was a problem with the login operation:', error);
    })
    .finally(() => {
    });
};
