// Function to verify JWT using the secret key
document.body.innerHTML = ''; // Clear previous content
// document.body.appendChild(splashWrapper); // Show splash screen

document.body.appendChild(dashWrapper);

// Function to get token from cookies
function getTokenFromCookie() {
    const name = 'token=';
    const decodedCookie = decodeURIComponent(document.cookie);
    const cookieArray = decodedCookie.split(';');

    for (let cookie of cookieArray) {
        cookie = cookie.trim();
        if (cookie.startsWith(name)) {
            return cookie.substring(name.length);
        }
    }
    return null; // Return null if no token is found
}

// Function to set the token in the URL as a query parameter
function setTokenInUrl(token) {
    const currentUrl = new URL(window.location.href);
    currentUrl.searchParams.set('token', token);
    window.history.replaceState({}, '', currentUrl); // Update the URL without reloading the page
}

// JWT verification function
async function verifyJWT(token) {
    const secretKey = new TextEncoder().encode("G$8a9Zq5@dR3^yX!7wKj*Ue2#fH6%tLp");
    try {
        const { payload } = await jwtVerify(token, secretKey); // Verifying the token
        console.log('Token is valid:', payload);

        // Set token in the URL after successful verification
        setTokenInUrl(token);

        // Continue with your application logic here, e.g., rendering dashboard
        document.body.innerHTML = ''; // Clear the body content
        document.body.appendChild(dashWrapper); // Append dashboard wrapper
        handleDashboard(); // Call dashboard handler function
    } catch (error) {
        console.error('Invalid token:', error);
        window.location.href = '/login'; // Redirect to login if the token is invalid
    }
}

// Initial flow to check and verify token
function initTokenFlow() {
    const tokenFromCookie = getTokenFromCookie();

    if (tokenFromCookie) {
        // If token is found in the cookie, proceed to verify it
        verifyJWT(tokenFromCookie); // Verify token from the cookie
    } else {
        // If token is not found in the cookie, redirect to login page
        console.log('Token not found in cookie. Redirecting to login.');
        window.location.href = '/login'; // Redirect to login if no token is found
    }
}
document.addEventListener('touchmove', function(event) {
    event.preventDefault();
}, { passive: false });
// Call the init flow to check token
initTokenFlow();
